<template>
    <div class="row mb-3">
        <div class="col">
            <h5>Progress Bar Element</h5>
            <div class="progress">
                <div class="progress-bar" role="progressbar" id="epoch-progress-bar" v-bind:style="{width: percentage + '%'}"
                    v-bind:aria-valuenow="percentage" aria-valuemin="0" aria-valuemax="100">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ProgressElement',
    props: {
        id: {type: String},
        data: {type: Object}
  },
  computed: {
    percentage() {
        var val = ((this.data.current / this.data.max) *100);
        console.log(val)
        return val
    }
  }
};
</script>

